@import '@base';

$form-btn-selected-color: rgba(122, 136, 155, 1);
$form-btn-hover-color: rgba(122, 136, 155, 0.192);

$form-submit-color: rgba(103, 183, 113, 1);
$form-submit-hover-color: rgb(36, 121, 47);

.shaded-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.603);
}

.Register {
  position: absolute;
  width: 320px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 5px;
  background-color: white;
}

.header {
  text-align: center;
  padding: 20px 0;
  border-bottom: 1px solid grey;
}

.body {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.buttons {
  display: flex;
}

%button {
  flex-basis: 50%;
  flex-grow: 1;
  padding: 9px 0;
  border: 1px solid rgba(122, 136, 155, 1);
  color: rgba(122, 136, 155, 1);
  border-radius: 5px;
  cursor: pointer;

  &:hover:not(.selected) {
    background-color: $form-btn-hover-color;
  }

  &.selected {
    background-color: $form-btn-selected-color;
    color: white;
  }
}

.login-btn {
  @extend %button;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.register-btn {
  @extend %button;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
}

.form-label {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.795);
}

%input {
  margin-top: 3px;
  padding: 9px 0 9px 7px;
  border: 1px solid rgba(0, 0, 0, 0.445);
  border-radius: 5px;
  // box-shadow: 0 0 1px black;
}

.email-field {
  @extend %input;
}

.password-field {
  @extend %input;
}

.submit-btn {
  margin-top: 15px;
  background-color: $form-submit-color;
  border-radius: 5px;
  padding: 9px 0;
  color: white;
  cursor: pointer;

  &:hover {
    background-color: $form-submit-hover-color;
  }
}
